<script>
import tableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

// 將搜尋內容轉小寫後，篩選符合結果
const toLower = (text) => {
  return text.toString().toLowerCase();
};
const searchByName = (items, term) => {
  if (term) {
    return items.filter(
      (item) =>
        toLower(item.proj).includes(toLower(term)) ||
        toLower(item.period).includes(toLower(term)) ||
        toLower(item.region).includes(toLower(term)) ||
        toLower(item.contract_count).includes(toLower(term)) ||
        toLower(item.status).includes(toLower(term)) ||
        toLower(item.useable).includes(toLower(term))
    );
  }
  return items;
};

export default {
  name: "engineeringDataManagement-component",
  components: { tableComponent, Pagination1 },
  data() {
    return {
      search: "", // 搜尋內容
      tableColumns: [
        { id: "index", label: "項次", width: "50" },
        { id: "proj", label: "工程編號 (工程名稱)", width: "300" },
        { id: "period", label: "建立日期 (開始 ~ 結束日期)", width: "220" },
        { id: "region", label: "工作區域", width: "300" },
        { id: "contract_count", label: "總/下包廠商數", width: "125" },
        { id: "status", label: "工程狀態", width: "150" },
        { id: "useable", label: "可用狀態", width: "150" },
      ],
      data: [],
      tableData: [], // 表格資料 (All)
      tableList: [], // 表格資料 (單頁)

      dialogHint: false, // 視窗開關
      dialogHintId: 0, // 檢視簽核明細
    };
  },
  methods: {
    async getEngineeringData() {
      try {
        const res = await this.$store.dispatch("getEngineeringData");
        const data = res.data.data;
        this.data = JSON.parse(JSON.stringify(data));

        // 將取得的資料，做顯示前加工處理
        this.data.forEach((e, index) => {
          e.index = index + 1;
          e.proj = `${e.project_No} (${e.project_Name})`;
          e.period = `${e.create_Date} (${e.begin_Date} ~ ${e.end_Date})`;
          e.region = `${e.LD2} / ${e.LD1} / ${e.LD4} / ${e.LD3} / ${e.floor}`;
          e.contract_count = `${e.tcount}家/${e.scount}家`;
          e.status = this.convertStatus(e.project_Status);
          e.useable =
            e.deleted == 1
              ? "已刪除(廠商不可申請每日工單)"
              : e.project_Status == "WAITAPP"
              ? "簽核中"
              : e.project_Status == "APPROVE"
              ? "廠商可申請每日施工單"
              : "廠商不可申請每日工單";
        });
        this.tableData = this.data;
        this.tableList = this.tableData.slice(0, 10);
      } catch (err) {
        console.log(err);
      }
    },

    // 篩選搜尋之內容，並呈現表格第一頁
    searchInput(evt) {
      if (evt.code === "Enter" || evt.code === "NumpadEnter") {
        const val = evt.target.value;
        this.tableData = searchByName(this.data, val);
        this.tableList = this.tableData.slice(0, 10);
        this.$store.state.currentPage = 1;
      }
    },
    // 切換資料的狀態 (刪除/復原)
    async dialogSwitchHandler(id) {
      try {
        let res = await this.$store.dispatch("switchEngineeringData", id);
        if (res.data.isOK) {
          this.$message({
            type: "success",
            duration: "5000",
            showClose: true,
            message: "操作成功",
          });
          // 打 API 重取資料，呈現表格第一頁
          this.getEngineeringData();
          this.$store.state.currentPage = 1;
        }
      } catch (err) {
        console.log(err);
      }
    },

    // 開啟視窗，並記錄欲開的頁面參數
    hintDialog(id) {
      this.dialogHint = true;
      this.dialogHintId = id;
    },
    cancelHintDialog() {
      this.dialogHint = false;
    },
    // 開新畫面 token 會被清除，故需事先取 API 資料
    async intoViewSignOffDetails() {
      this.dialogHint = false;
      let data = [];
      try {
        const getProjDetailRes = await this.$store.dispatch(
          "getProjDetail",
          this.dialogHintId
        );
        const getSignOffHistoryRes = await this.$store.dispatch(
          "getSignOffHistory",
          this.dialogHintId
        );
        // [工程單及承包商資料、簽核歷程資料]
        data = [getProjDetailRes.data.data, getSignOffHistoryRes.data.data];
      } catch (err) {
        console.log(err);
      }

      // 以 query 夾帶 JSON 字串傳送資料
      const { href } = this.$router.resolve({
        path: "/viewSignOffDetails",
        query: {
          data: JSON.stringify(data),
        },
      });
      window.open(href, "_blank");
    },

    // 依據頁數，更新對應的 tableList 資料
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
  },
  async mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    // 打 API 取得資料，呈現表格第一頁
    this.getEngineeringData();
    this.$store.state.currentPage = 1;
  },
};
</script>

<template src="./engineeringDataManagement.html"></template>
